import React from 'react';
import propType from 'prop-types';
import styled from 'styled-components';
import Section from '../../Components/Section';
import Loader from '../../Components/Loader';
import Message from '../../Components/Message';
import Poster from '../../Components/Poster';

const Container = styled.div`
padding-top : 100px;
left : 5%;`


const Form = styled.form`
margin-top : 70px;
width : 100%;
margin-left : 15px;`


const Input = styled.input`
margin-top : 30px;
all : unset;
font-size : 30px;
width : 100%;`



searchPresenter.propType = {
    tvResults : propType.array,
    movieResults : propType.array,
    searchTerm : propType.string.isRequired,
    error: propType.bool,
    loading: propType.bool};




function searchPresenter(props) {
    const {tvResults,
        movieResults,
        //searchTerm,
        //error,
        loading,
    handleSubmit,
    updateTerm} = props;

    return(<>
    <Form onSubmit = {handleSubmit}><Input type = 'text' 
    onChange = {updateTerm} 
    placeholder = "ÏòÅÌôîÎÇò ÌîÑÎ°úÍ∑∏Îû®ÏùÑ Í≤ÄÏÉâÌïòÏÑ∏Ïöî">
        </Input>
    </Form>
    {loading ? (<Loader />) : (<>
        {movieResults && movieResults.length > 0 && (
            <Section title ='ÏòÅÌôî' 
            children = {movieResults.map(movie => <Poster 
                title = {movie.title} 
                id = {movie.id} 
                key = {movie.id}
                imageUrl = {movie.poster_path} 
                isMovie ={true}
                rating = {movie.vote_average}/>)}
                />)
            }
            {movieResults && movieResults.length === 0 && (<Message text = 'Í≤ÄÏÉâÎêú ÏòÅÌôîÍ∞Ä ÏóÜÏäµÎãàÎã§.'/>)}
        {tvResults && tvResults.length > 0 && (
            <Section title ='TV' 
            children = {tvResults.map(tv => <Poster 
                title = {tv.name} 
                id = {tv.id}
                key = {tv.id} 
                imageUrl = {tv.poster_path} 
                isMovie ={false}
                rating = {tv.vote_average}/>)} />
            )}
        
        {tvResults && tvResults.length === 0 &&(<Container><Message text = 'Í≤ÄÏÉâÎêú Î∞©ÏÜ°ÌîÑÎ°úÍ∑∏Îû®Ïù¥ ÏóÜÏäµÎãàÎã§.' /></Container>)}    
        </>
    )}
    </>
    )
};




export default searchPresenter;